<script>
import { search } from './service' 
import Vue from 'vue';
import { Tab, Tabs,Toast,Sidebar ,SidebarItem} from 'vant';

Vue.use(Sidebar);
Vue.use(SidebarItem);
Vue.use(Toast);
Vue.use(Tab);
Vue.use(Tabs);
export default {
components: {},
data() {
    return {
        activeKey:0,
        active:0,
        isSearch:false,
        keyWord:'',
        searchData:[],
        total:0,
        form:{
            keyword:'前端',
            page_size:1000,
            type:null,
        }
    };
},
computed: {},
watch: {},
filters:{
    entityType:function(type){
        let name = '';
        switch(type){
            case 'expertise':
                 name = '猛人';
                 break;
            case 'method':
                 name = '办法';
                 break;
            case 'demand':
                 name = '需求';
                 break; 
             default :
                name = "其他";
                break;                   
        }
        return name;
    }
},
methods: {
    typeChange(val){
        console.log(val);
        if(val===0){
            this.form.type=null;
        }else if(val===1){
            this.form.type='awesome';
        }else if(val===2){
            this.form.type='method';
        }else{
            this.form.type='demand';
        }
        this.handelSearch();
    },
    handelSearch(){
        this.isSearch = true;
        Toast.loading({
            message: '加载中...',
            forbidClick: true,
            loadingType: 'spinner',
        });
        search(this.form).then(res=>{
            console.log(res);
            res.data.forEach(item => {
                    if(item.user){
                        item.author = item.user.author;
                    }
            });
            this.searchData = res.data;
            this.total = res.pagination.total_count;
            Toast.clear();
        }).catch(res=>{
            Toast.clear();
            Toast(res.message);            
        })

    }
},
created() {

},
mounted() {
    this.handelSearch();
},
beforeCreate() {},
beforeMount() {},
beforeUpdate() {},
updated() {},
beforeDestroy() {},
destroyed() {},
activated() {},
}
</script>
